import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/crossing/:id",
    name: "Crossing",
    component: () =>
      import(/* webpackChunkName: "map" */ "../views/Crossing.vue"),
  },
  {
    path: "/crossing/:id/timetable",
    name: "Crossing_timetable",
    component: () =>
      import(/* webpackChunkName: "map" */ "../views/TimeTable.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
