<template>
  <div ref="map-root"></div>
</template>

<script>
import router from "@/router"

import View from "ol/View"
import Map from "ol/Map"
import TileLayer from "ol/layer/Tile"
import OSM from "ol/source/OSM"
import VectorLayer from "ol/layer/Vector"
import VectorSource from "ol/source/Vector"
import { Circle as CircleStyle, Fill, Stroke, Style, Text } from "ol/style"
import Cluster from "ol/source/Cluster"
import GeoJSON from "ol/format/GeoJSON"
import { fromLonLat } from "ol/proj/"
import { extend } from "ol/extent"
import "ol/ol.css"

// Crossings data
import Crossings from "../assets/crossings.geojson"

export default {
  name: "MapContainer",
  components: {},
  props: {},
  mounted() {
    // Prepare crossings data
    const crossingsFeatures = new GeoJSON().readFeatures(Crossings, {
      featureProjection: "EPSG:3857",
    })
    const crossingsSource = new Cluster({
      distance: 50,
      minDistance: 1,
      source: new VectorSource({
        features: crossingsFeatures,
      }),
    })

    // Prepare layer for displaying crossings
    const styleCache = {}
    const crossingsLayer = new VectorLayer({
      source: crossingsSource,
      style: function(feature) {
        const size = feature.get("features").length
        let style = styleCache[size]
        if (!style) {
          style = new Style({
            image: new CircleStyle({
              radius: 10,
              stroke: new Stroke({
                color: "#fff",
              }),
              fill: new Fill({
                color: size > 1 ? "#006fb0" : "#e3b210",
              }),
            }),
            text: new Text({
              text: size > 1 ? size.toString() : "",
              fill: new Fill({
                color: "#fff",
              }),
            }),
          })
          styleCache[size] = style
        }
        return style
      },
    })

    // Create map
    const map = new Map({
      target: this.$refs["map-root"],
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
        crossingsLayer,
      ],

      view: new View({
        zoom: 7,
        center: fromLonLat([24.1052, 56.9496]),
        constrainResolution: true,
      }),
    })

    // cursor: pointer when hovering markers
    map.on("pointermove", function(e) {
      var pixel = map.getEventPixel(e.originalEvent)
      var hit = map.hasFeatureAtPixel(pixel)
      this.getTargetElement().querySelector("canvas").style.cursor = hit
        ? "pointer"
        : "default"
    })

    // Marker click interaction
    map.on("click", function(e) {
      map.forEachFeatureAtPixel(e.pixel, function(feature, layer) {
        if (feature.get("features").length > 1) {
          // Marker is cluster, zoom in
          let extent = feature
            .get("features")[0]
            .getGeometry()
            .getExtent()
            .slice(0)
          feature.get("features").forEach(function(feature) {
            extend(extent, feature.getGeometry().getExtent())
          })
          map.getView().fit(extent, { duration: 300 })
        } else {
          // Marker is crossing, open crossing view
          let path =
            "/crossing/" +
            feature
              .get("features")[0]
              .getId()
              .split("/")[1]
          router.push({
            path: path,
          })
        }
      })
    })
  },
}
</script>

<style scoped>
div {
  height: 1000px;
  width: 2000px;
}
</style>